.container {
  /* Standardizing Colors */
  --white: #fff;
  --primary-color: #ededed;
  --secondary-color: #324154;
  --black: #000;
  --terminal-black: #333;
  --terminal-green: #29bf83;
  --tomato: #e23e3e;

  /* Standardizing Sizes */
  --container-width: 90rem;

  /* Standardizing Spacing */
  --space-1: 0.1rem;
  --space-2: 0.2rem;
  --space-4: 0.4rem;
  --space-6: 0.6rem;
  --space-8: 0.8rem;
  --space1: 1rem;
  --space1-2: 1.2rem;
  --space1-4: 1.4rem;
  --space1-6: 1.6rem;
  --space1-8: 1.8rem;
  --space2: 2rem;
  --space4: 4rem;
  --space8: 8rem;

  /* Standardizing fonts */
  --font-small: 1.2rem;
  --font-medium: 1.4rem;
  --font-large: 1.6rem;
  --font-bold: bold;
  --line-height: 1.6;
  --font-family-secondary: monospace;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: var(--container-width);
  margin: 0 auto;
}
